import React, { Component } from 'react';
import './App.css';

class Subject extends Component {
  render() {

    return (
      <header>
        <h1><a href="/" onClick={function(e){
          e.preventDefault();
          this.props.onChangePage();
        }.bind(this)}>{this.props.title}</a></h1>
        {this.props.sub}
      </header>
    );
  }
}
class Toc extends Component {
  render() {
    let list = [];
    let data = this.props.data;
    let i = 0;
    while(i < data.length){
      list.push(
        <li key={data[i].id}>
          <a 
            href={"/contenst/" + data[i].id}
            data-id = {data[i].id}
            onClick={function(e){
              e.preventDefault();
              this.props.onChangePage(e.target.dataset.id);
            }.bind(this)}
          >{data[i].title}</a>
        </li>);
      i++;
    }
    return(
      <nav>
        <ul>
          {list}
        </ul>
      </nav>
    );
  }
}
class Content extends Component {
  render() {
    return(
      <article>
        <h2>{this.props.title}</h2>
        {this.props.desc}
      </article>
    );
  }
}

class Control extends Component {
  render(){
    return(
      <ul>
        <li><a href="/create">create</a></li>
        <li><a href="/delete">delete</a></li>
        <li><input type="button" value="delete"></input></li>
        
      </ul>
    );
  }
} 
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      mode: 'read',
      select_number: 2,
      subject: {title: 'WEB', sub: 'World wide Web'},
      welcome: {title: 'Welcom', desc: 'welcom to REACT!!'},
      contents: [
        {id:1, title:'HTML', desc:'HTML is Hyper TEXT'},
        {id:2, title:'CSS', desc:'CSS is Hyper TEXT'},
        {id:3, title:'JAVASCRITP', desc:'JAVASCRITP is Hyper TEXT'}
      ]
    }

  }
  render() {
    let _title, _desc = null;
    if(this.state.mode === 'welcome'){
      _title = this.state.welcome.title;
      _desc = this.state.welcome.desc;
    }else if(this.state.mode === 'read'){
      let i = 0;
      
      while(i < this.state.contents.length){
        let data = this.state.contents[i];
        if(data.id === this.state.select_number){
          _title = data.title;
          _desc = data.desc;
          break;
        }
        i++;
      }
    }
    return (
      <div className="App">
        <Subject 
          title={this.state.subject.title} 
          sub={this.state.subject.sub}
          onChangePage = {function(){
            this.setState({
              mode: 'welcome'
            });
          }.bind(this)}
          >
        </Subject>
        
        <Toc 
        data={this.state.contents}
        onChangePage = {function(id){
          this.setState({
            mode: 'read',
            select_number: Number(id)
          });
        }.bind(this)}
        ></Toc>
        <Control></Control>
        <Content title={_title} desc={_desc}></Content>
      </div>
    );
  }
}

export default App;
